a{
    text-decoration: none;
    color: #092819;
}
.header{
    background-color: #fde7b3;
    height: 650px;
    padding: 10px 100px 0px;
    
}
nav{
    z-index: 1;
    background-color: #fde7b3;
    position: fixed;
    padding: 10px 100px 0px;
    width: 100%;
    display: flex;
    gap: 40px;
    align-items: center;
    height: 70px;
    margin-bottom: 0;
}
.logo{
    display: flex;
    /* margin-left: 40px; */
}
.title > h1{
    color: #137335;
    margin: 0px;
    font-size: 35px;
    font-weight: 800;
    letter-spacing: 1.42px;
    font-family: cursive;
    padding-bottom: 0px;
   

}
.title > p{
    margin-top: 0;
    font-size: 14px;
    font-weight: 500;
    color: #666666;
    letter-spacing: 1.42px;
}

.nav-menu{
    display: flex;
    align-items: center;
    gap: 20px;
    list-style: none;
    cursor: pointer;
}
.nav-list{
    line-height: 1.08;
    letter-spacing: 1.42px;
    color: #666666;
    font-size: 15px;
    font-weight: 500;
    padding: 2px 0;
    position: relative;
    margin: 20px;

}
.nav-list:hover{
    color: #092819 ;
}
.nav-list::after{
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    background-color: #123524;
    bottom: -6px;
    right: 0px;
    left: 50%;
    display: block;
    border-radius: 0px 0px 4px 4px;
    transform-origin:left center;
    transform: translateX(-50%);
    transition: all 250ms cubic-bezier(0.025, 0.46, 0.45, 0.94) 0s;
}

.nav-list:hover::after{
    width: 25px;
}
.nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    font-size: 1.8rem;
    outline: none;
    visibility: hidden;
    display: none;
    opacity: 0;
    white-space: nowrap;
}
.cta{
    color: #666666;
    font-size: 15px;
    font-weight: 500;
    border: 1.5px solid  #092819;
    padding: 8px 18px !important;
    border-radius: 0.3px 8px 8px 8px ;
    display: inline-block;
    white-space: nowrap;

}
.nav-list{
    white-space: nowrap;
}
.cta:hover{
    color: #092814;
    transition: all 250ms cubic-bezier(0.025, 0.46, 0.45, 0.94) 0s;
}
.content{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0px;
}
.text{
    color: #092819;
    font-size: 45px;
    font-weight: bolder;
    line-height: 1.1;
}
.text > span{
    color: #137335;
}
.description{
   animation: scrollLeft 2s ease-out forwards;   
}
.description > p{
   font-size: 14px;
   width: 480px;
   color: #666666;
   margin-top: 0px;
 
}
.content-img{
    width: 650px;
    height: 500px;
    animation: scrollRight 2s ease-out forwards;

}
.show-right-scroll{
    animation: scrollRight 2s ease-out forwards;
}
.show-left-scroll{
    animation: scrollLeft 2s ease-out forwards;
}

@keyframes scrollRight { 
    0%{
        transform: translateX(100%)
    }
    100%{
        transform: translateX(0)
    }
    
}
@keyframes scrollLeft { 
    0%{
        transform: translateX(-100%)
    }
    100%{
        transform: translateX(0)
    }
    
}
.checkus{
display: flex;
text-align: center;
align-items: center;

}
.northeast{
    align-items: center;
    font-size: 12px;
    top: 10px;
}
.cta2{
    background-color:#f1ad10 ;
    text-align: center;
    border-radius: 0px 8px 8px 8px;
    padding: 8px 25px;
    font-weight: 500;
    font-size: 12px;
    margin-right: 25px;
    cursor: pointer;
}
.cta2:hover{
    opacity: 0.5;
    transition: all 250ms cubic-bezier(0.025, 0.46, 0.45, 0.94) 0s;
}
.wos{
    display: flex;
    align-items: center;
}
.play{
   font-size: 25px;
   color: #092819;
}
.wos > p{
    padding-left: 5px;
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: 400;
}

.details{
   display: flex;
   gap: 20px;
   margin-top: 15px;
}
.details > span > h3{
color: #092819;
font-weight: bolder;
margin-bottom: 0;
}
.details > span > p{
    margin-top: 8px;
    font-size: 13px;
    color: #666666;
}
